import { Sequelize, DataTypes, Model } from "sequelize";
import sequelize from '../../middlewares/dbInstance'
import Student from './Student'
import Course from './Course'


console.log('Course', Course)
const Zroll = sequelize.define('Zroll', {
  course_id: {
    type: DataTypes.NUMBER,
    references: {
      model: Course,
      key: 'id'
    }
  },
  student_id: {
    type: DataTypes.NUMBER,
    references: {
      model: Student,
      key: 'id'
    }
  }
}, {
  tableName: 'students',
  timestamps: false
})

Zroll.belongsTo(Course)
// Zroll.belongsTo(Student)

export default Zroll